.simple-card {
  display: inline-block;
  width: 12vw;
  min-width: 150px;

  .ant-card-meta-description {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .simple-card-preview {
    object-fit: cover;
    width: 12vw;
    height: 16vw;
    min-width: 150px;
    min-height: 200px;
  }
}

